import React from "react"

// Components
import { Link } from "gatsby"
import { Image, Layout, Button } from "../../components"

// Data
import data from "../../data/pages/services.json"
import services from "../../data/services.json"

const Services = () => {
  const content = data.content

  return (
    <Layout pageContext={data}>
      <div className="hero">
        <div style={{ width: "250px" }}>
          <Image src={content.hero.imagePath} />
        </div>
        <h3>{data.title}</h3>
        <h2>{content.hero.title}</h2>
        <h3>{content.hero.secondaryTitle}</h3>
        <p>{content.hero.text}</p>
      </div>

      <div className="service-list">
        {Object.entries(services.groups).map(([key, value]) => (
          <div key={key} className={`service-group ${key}`}>
            <b><h3>{value.title}</h3></b>
            {Object.values(services.data)
              .filter(service => service.group === key)
              .map(service => (
                <div key={service.path} className="service-card">
                  <div style={{ width: "250px" }}>
                    <Image src={service.cardImagePath} />
                  </div>
                  <h3>{service.title}</h3>
                  <p>{service.blurb}</p>
                  <Link to={service.path}>Learn More</Link>
                </div>
              ))}
          </div>
        ))}
      </div>

      <div className="statement">
        <h2>{content.statement.title}</h2>
        <p>{content.statement.text}</p>
        <Button to={content.statement.cta.src}>
          {content.statement.cta.label}
        </Button>
      </div>

      <div className="endHero" style={{ width: "250px" }}>
        <Image src={content.endHero.imagePath} />
        <h3>{content.endHero.title}</h3>
        <p>{content.endHero.text}</p>
        <Button to={content.endHero.cta.src}>
          {content.endHero.cta.label}
        </Button>
      </div>
    </Layout>
  )
}

export default Services
